/* breadcrumb */
.breadcrumbs {
  margin: 0;
  padding: 0;
  font-size: 13px;
  flex-wrap: wrap;
  color: #8D8D8D;
  display: none;
  justify-content: center;
  letter-spacing: 0.05em;
  white-space: nowrap;
  color: rgba(255, 255, 255, 0.7);
  padding-top: 10px;
  position: relative;
  overflow: hidden;
  text-overflow: ellipsis; }
  .breadcrumbs::before {
    height: 1px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    background: rgba(255, 255, 255, 0.2);
    width: 286px;
    content: ''; }
  .breadcrumbs__item {
    list-style: none;
    margin: 0 0 5px 0; }
    .breadcrumbs__item + .breadcrumbs__item {
      margin-left: 10px; }
      .breadcrumbs__item + .breadcrumbs__item::before {
        content: '/';
        margin-right: 10px; }
    .breadcrumbs__item > a, .breadcrumbs__item > span {
      display: inline-block;
      line-height: 14px;
      text-decoration: none;
      position: relative; }
    .breadcrumbs__item > a {
      color: rgba(255, 255, 255, 0.4);
      padding-right: 10px;
      color: rgba(255, 255, 255, 0.4); }
      .breadcrumbs__item > a::before {
        content: '\e913';
        position: absolute;
        font-family: "icomoon";
        right: 0;
        font-size: 5px;
        top: 6px;
        line-height: 1; }
    .breadcrumbs__item:first-child > a {
      padding-right: 0; }
      .breadcrumbs__item:first-child > a::before {
        display: none; }
  @media (min-width: 768px) {
    .breadcrumbs {
      display: flex; } }
  @media (min-width: 1280px) {
    .breadcrumbs__item > a:hover {
      color: #ffffff; } }

/* pagination */
.pagination {
  font-size: 1rem;
  text-align: center;
  margin-bottom: 0px;
  font-weight: 500; }
  .pagination ul {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    width: 100%; }
    .pagination ul > li {
      list-style: none;
      margin: 0;
      display: inline-block;
      line-height: 1;
      vertical-align: middle;
      padding: 0;
      flex-shrink: 0; }
      .pagination ul > li::before {
        display: none; }
      .pagination ul > li.pagination__prev, .pagination ul > li.pagination__next {
        font-size: 0.875rem; }
        .pagination ul > li.pagination__prev > a, .pagination ul > li.pagination__prev > span, .pagination ul > li.pagination__next > a, .pagination ul > li.pagination__next > span {
          border-color: transparent; }
          .pagination ul > li.pagination__prev > a::before, .pagination ul > li.pagination__prev > span::before, .pagination ul > li.pagination__next > a::before, .pagination ul > li.pagination__next > span::before {
            font-family: "icomoon";
            vertical-align: 0;
            line-height: 1;
            display: inline-block; }
      .pagination ul > li.pagination__prev {
        margin-right: 0px; }
        .pagination ul > li.pagination__prev > a::before, .pagination ul > li.pagination__prev > span::before {
          content: '\e901'; }
      .pagination ul > li.pagination__next {
        margin-left: 6px; }
        .pagination ul > li.pagination__next > a::before, .pagination ul > li.pagination__next > span::before {
          content: '\e902'; }
      .pagination ul > li + li {
        margin-left: 0px; }
      .pagination ul > li > a, .pagination ul > li > span {
        display: inline-block;
        width: 50px;
        height: 50px;
        text-align: center;
        line-height: 50px;
